'use client'

import { motion } from 'framer-motion';
import { useState } from 'react';  

import styles from './page.module.scss'
import useMousePosition from './utils/useMousePosition';

import logo from './logo.svg';
import { isMobile } from 'react-device-detect';

export default function Home() {

  const [isHovered, setIsHovered] = useState(false);
  const { x, y } = useMousePosition();
  const size = (isHovered ? 400 * (isMobile ? 0.7 : 1) : 40);

  const maskPositionX = isNaN(x) ? x - (size/2) : window.innerWidth / 2 - 110;
  const maskPositionY = isNaN(y) ? y - (size/2) : window.innerHeight / 2 - 140;
  return (
    <main className={styles.main}>
      <motion.div 
        className={styles.mask}
        animate={{
          WebkitMaskPosition: `${maskPositionX}px ${maskPositionY}px`,
          WebkitMaskSize: `${size}px`,
        }}
        transition={{ type: "tween", ease: "backOut", duration:0.5}}
      >
        <div onMouseEnter={() => {setIsHovered(true)}} onMouseLeave={() => {setIsHovered(false)}}>
          <div className={styles.body}>
            <img src={logo} className="App-logo" alt="logo" style={{height: '40vmin'}}/>
            <p style={{
              width: window.innerWidth / 3,
              fontSize: `${isMobile ? 32 : 64}px`,
              lineHeight: `${isMobile ? 33 : 66}px`,
            }}>
              <span>Spring Camp 2024</span><br />coming soon...
            </p>
          </div>
        </div>
      </motion.div>

    </main>
  )
}